@import url(https://fonts.googleapis.com/css?family=Dancing+Script);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.days_wrapper {
  margin: 0;
  padding: 0;
}

._days {
  font-size: 1.5rem;
}

._hours,
._title {
  font-size: 1rem;
}

.App {
  text-align: center;
  box-sizing: border-box;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  background-image: url(/lovedays/static/media/bg.616eceaf.png);
  background-position: 77% 69%;
  min-height: 667px;
  min-width: 320px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
}

.title {
  position: absolute;
  top: 1rem;
  font-family: "Dancing Script", cursive;
}

.lovedays-wrapper {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid white;
  z-index: 2;
}

.profile-wrapper {
  position: absolute;
  bottom: 2rem;
  display: flex;
  justify-content: space-around;
  text-align: center;
  background-image: -webkit-linear-gradient(
    330deg,
    rgb(252, 203, 144) 0%,
    rgb(213, 126, 235) 100%
  );
  background-image: linear-gradient(
    120deg,
    rgb(252, 203, 144) 0%,
    rgb(213, 126, 235) 100%
  );
  border-radius: 10px;
  padding: 5px;
  opacity: 50;
  font-family: "Dancing Script", cursive;
  font-size: 1.2rem;
}
.heart {
  display: flex;
  align-items: center;
  color: red;
  font-size: 1.5rem;
}
._heart {
  -webkit-animation: heartBeat 1s ease-in-out infinite;
          animation: heartBeat 1s ease-in-out infinite;
}

@-webkit-keyframes heartBeat {
  0% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }

  45% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  70% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }

  100% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}

@keyframes heartBeat {
  0% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }

  45% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  70% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }

  100% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}

.guy_pic,
.girl_pic {
  border: 1px solid white;
  border-radius: 50%;
  width: 80px;
}

._theguy,
._thegirl {
  height: 120px;
  width: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

